import VueRouter from 'vue-router'
const router = new VueRouter({
    routes: [
        { path: '/', redirect: { name: 'Welcome' } },
        { path: '/welcome', name: "Welcome", component: () => import('../pages/welcome') },
        {
            path: '/home', name: "Home", component: () => import('../pages/home.vue'),redirect: { path: '/home/tree' },
            // children: [
            //     {
            //         path: '/home', name: "Home", component: () => import('../pages/home.vue'),
            //     }
            // ]
            children: [
                {
                    path: 'tree', name: "Tree", component: () => import('../pages/tree/index.vue'), redirect: { path: 'tree/base1' },
                    children: [
                        {
                            path: 'base1', component: () => import('../pages/tree/kana-1'),
                        },
                        {
                            path: 'base-test1', component: () => import('../pages/tree/kana-test1'),
                        },
                        {
                            path: 'base2', component: () => import('../pages/tree/kana-2'),
                        },
                        {
                            path: 'base3', component: () => import('../pages/tree/kana-3'),
                        },
                        {
                            path: 'base4', component: () => import('../pages/tree/kana-4'),
                        },
                        {
                            path: 'base5', component: () => import('../pages/tree/kana-5'),
                        },
                    ]
                },
                {
                    path: 'index', name: "Index", component: () => import('../pages/lookup/index'),
                },
                {
                    path: 'contact', name: "Contact", component: () => import('../pages/contact/index'),
                },
            ]
        },
    ]
})

export default router
